import { Component, OnInit } from '@angular/core';
import { AgentifaiPluginControllerService } from 'src/app/services/agentifai/agentifai-plugin-controller.service';

@Component({
  selector: 'app-second-page',
  templateUrl: './second-page.page.html',
  styleUrls: ['./second-page.page.scss'],
})
export class SecondPagePage implements OnInit {

  constructor(
    private agentifaiController: AgentifaiPluginControllerService,
  ) { }

  ngOnInit() {}

  ionViewDidEnter() {
    this.agentifaiController.showWidget()
  }

  ionViewDidLeave() {
    this.agentifaiController.hideWidget();
  }

}
